@import "vars";
@import "mixins";

.vision {
  position: relative;
  background: #fff;

  &__heading {
    width: fit-content;
    margin: 0 auto;

    font-family: Poppins, Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-size: 52px;
    color: $heading_color;
  }

  &__text {
    margin: 0 auto;
    padding: 25px;
    width: 600px;

    font-family: Open Sans, Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    text-align: center;
    color: $text_color;
  }

  &__button {
    margin-top: 40px;
    position: absolute;
    left: calc(50% - 115px);
  }
}

.item--small {
  @include bg-items(50px, 2px, $secondcolor);

  &:nth-of-type(1) {
    top: 10%;
    left: 20%;
  }
  
  &:nth-of-type(2) {
    top: 15%;
    right: 30%;
  }
  
  &:nth-of-type(3) {
    top: 80%;
    right: 30%;
  }
  
  &:nth-of-type(4) {
    top: 70%;
    left: 30%;
  }
}

.item--medium {
  @include bg-items(100px, 4px, $secondcolor);

  &:nth-of-type(5) {
    top: 10%;
    left: 50%;
  }
  
  &:nth-of-type(6) {
    top: 80%;
    left: 10%;
  }
  
  &:nth-of-type(7) {
    top: 55%;
    right: 10%;
  }
  
  &:nth-of-type(8) {
    top: 35%;
    left: 10%;
  }
}

.item--big {
  @include bg-items(150px, 5px, $secondcolor);

  &:nth-of-type(9) {
    top: 30%;
    right: 10%;
  }
  
  &:nth-of-type(10) {
    top: 80%;
    right: 50%;
  }
  
  &:nth-of-type(11) {
    bottom: 40%;
    left: 15%;
  }
  
  &:nth-of-type(12) {
    top: 55%;
    right: 30%;
  }
}

@media screen and(max-width: $small_screen) {
  .vision {
    height: 500px;

    &__heading {
      font-size: 32px;
      text-align: center;
    }

    &__text {
      width: 250px;
    }
  }
}
